@media screen and (min-width: 800px) {
  .App {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(6, minmax(150px, 1fr));
  }
  .Movie {
    margin: 20px;
    display: grid;
    grid-auto-flow: column;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    grid-column: span 3;
    box-sizing: border-box;
    max-width: 1000px;
    width: 100%;
    justify-self: center;
  }
}
@media screen and (max-width: 800px) {
  .App {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(4, minmax(100px, 1fr));
  }
  .Movie {
    margin-left: 15px;
    margin-top: 70px;
    display: grid;
    grid-auto-flow: column;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    grid-column: span 4;
    box-sizing: border-box;
    max-width: 600px;
    width: 100%;
  }
}

.movie__title {
  font-size: 1.8em;
}
.Movie__Poster {
  position: relative;
  top: -30px;
  margin-right: 20px;
  margin-left: 20px;
}
.Movie__Synopsis {
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.5;
  height: 6em;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 10;
  -webkit-box-orient: vertical;
}

.Movie__Genre {
  font-weight: 800;
}

.Movie__Genre,
.Movie__Synopsis {
  color: rgba(0, 0, 0, 0.5);
  font-size: 1.3em;
  padding: 5px;
}
